.container {
  margin: auto;
  max-width: 600px;
}

img {
  width: 25px;
}

button {
  cursor: pointer;
}

.done {
  text-decoration: line-through;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  font-size: 25px;
  width: 100%;
  padding: 15px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card {
  box-shadow: 0 2px 7px 1px rgba(0, 0, 0, 0.2);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.addButton {
  width: 50px;
  height: 50px;
  font-size: 20px;
  border: none;
  background-color: skyblue;
  color: white;
  border-radius: 25px;
}
.modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hide{
  display: none;
}
.cardModal {
  padding: 20px;
  background-color: white;
}

.button {
  background-color: skyblue;
  border: 1px solid black;
}
